/* SymptomChecker.css */

/* Styling for the container */
.symptom-checker {
  text-align: center;
  color: #007bff;
}

/* Styling for the title */
.symptom-checker h1 {
  color: #007bff; /* Blue color */
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

/* Styling for the select dropdown */
.select-dropdown {
  width: 100%;
  margin-bottom: 1.5rem;
  color: #007bff;
  font-family: "Lucida Console", "Courier New", monospace;
  font-weight: strong;
}

.symptom-checker button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #dc3545; /* Red color */
border-color: #dc3545; /* Red color */
color: #fff; /* White color */
}

/* Styling for the diseases found section */
.symptom-list {
  margin-top: 2rem;
  
}

/* Styling for each disease item */
.symptom-item {
  font-size: 1.1rem;
  padding: 0.75rem 1.25rem;
  border: 1px solid #cfd3d3; /* Gray color */
  border-radius: 0.25rem;
  margin-bottom: 0.5rem;
}

/* Styling for strong tags within disease items */
.symptom-item strong {
  color: #dc3545; /* Red color */
}
.symptom-checker button {
  background-color: hsla(120, 90%, 27%, 0.938); /* Green */
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  margin: 4px 2px;
  cursor: pointer;
  padding: 14px 40px;
  border-radius: 8px;
}
.symptomchecker {
  color: #98b1e7;
}

